<template><section><h1>消息弹窗</h1>
<hr>
<h3>基础用法</h3>
<p>该弹窗样式风格适用于巡查端、管理端</p>
<div class="demo-block">
   <w-button @click="showMessageDefault">默认信息</w-button>
   <w-button @click="showMessageNormal">一般信息</w-button>
   <w-button @click="showMessageWarning">警告信息</w-button>
   <w-button @click="showMessageAnomalous">系统异常</w-button>
   <w-button @click="showMessageError">错误信息</w-button>
</div>

<demo-block>
                        <div slot="desc"><html><head></head><body><div class="demo-block">
   <w-button @click="showMessageDefault">默认信息</w-button>
   <w-button @click="showMessageNormal">一般信息</w-button>
   <w-button @click="showMessageWarning">警告信息</w-button>
   <w-button @click="showMessageAnomalous">系统异常</w-button>
   <w-button @click="showMessageError">错误信息</w-button>
</div>



</body></html></div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-block"</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessageDefault"</span>&gt;</span>默认信息<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessageNormal"</span>&gt;</span>一般信息<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessageWarning"</span>&gt;</span>警告信息<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessageAnomalous"</span>&gt;</span>系统异常<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">w-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"showMessageError"</span>&gt;</span>错误信息<span class="hljs-tag">&lt;/<span class="hljs-name">w-button</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span> <span class="hljs-attr">scoped</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span>{
    <span class="hljs-attr">methods</span>:{
      showMessageDefault(){
        <span class="hljs-keyword">this</span>.$wmessage.show();
      },
      showMessageNormal(){
        <span class="hljs-keyword">this</span>.$wmessage.show({
          <span class="hljs-attr">type</span>: <span class="hljs-string">'normal'</span>,
          <span class="hljs-attr">info</span>: <span class="hljs-string">'这是一条一般信息'</span>,
          <span class="hljs-attr">sure</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>)</span>{
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'我点击了确定按钮!'</span>);
          }
        });
      },
      showMessageWarning(){
        <span class="hljs-keyword">this</span>.$wmessage.show({
          <span class="hljs-attr">type</span>: <span class="hljs-string">'warning'</span>,
          <span class="hljs-attr">info</span>: <span class="hljs-string">'这是一条警告信息'</span>,
          <span class="hljs-attr">sure</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>)</span>{
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'我点击了确定按钮!'</span>);
          },
          <span class="hljs-attr">cancel</span>: <span class="hljs-function"><span class="hljs-keyword">function</span>(<span class="hljs-params"></span>)</span>{
            <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'我点击了取消按钮！'</span>);
          }
        });
      },
      showMessageAnomalous(){
        <span class="hljs-keyword">this</span>.$wmessage.show({
          <span class="hljs-attr">type</span>: <span class="hljs-string">'anomalous'</span>,
          <span class="hljs-attr">info</span>: <span class="hljs-string">'这是一条异常信息'</span>
        });
      },
      showMessageError(){
        <span class="hljs-keyword">this</span>.$wmessage.show({
          <span class="hljs-attr">type</span>: <span class="hljs-string">'error'</span>,
          <span class="hljs-attr">info</span>: <span class="hljs-string">'这是一条错误信息'</span>
        });
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

</code></pre>
</div></demo-block>
<h3>Api config参数</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>信息弹窗类型</td>
<td>String</td>
<td>normal, warning, anomalous, error</td>
<td>normal</td>
</tr>
<tr>
<td>info</td>
<td>弹出信息</td>
<td>String</td>
<td>所有字符类型文字</td>
<td>这是一条弹窗信息！</td>
</tr>
<tr>
<td>sure</td>
<td>点击确定按钮触发的回调函数</td>
<td>Function</td>
<td>Null</td>
<td>Null</td>
</tr>
<tr>
<td>cancel</td>
<td>点击取消按钮触发的回调函数</td>
<td>Function</td>
<td>Null</td>
<td>Null</td>
</tr>
</tbody>
</table>
</section></template>

<script scoped>
  export default{
    methods:{
      showMessageDefault(){
        this.$wmessage.show();
      },
      showMessageNormal(){
        this.$wmessage.show({
          type: 'normal',
          info: '这是一条一般信息',
          sure: function(){
            console.log('我点击了确定按钮!');
          }
        });
      },
      showMessageWarning(){
        this.$wmessage.show({
          type: 'warning',
          info: '这是一条警告信息',
          sure: function(){
            console.log('我点击了确定按钮!');
          },
          cancel: function(){
            console.log('我点击了取消按钮！');
          }
        });
      },
      showMessageAnomalous(){
        this.$wmessage.show({
          type: 'anomalous',
          info: '这是一条异常信息'
        });
      },
      showMessageError(){
        this.$wmessage.show({
          type: 'error',
          info: '这是一条错误信息'
        });
      }
    }
  }
</script>


